import React, { useState, useContext, useEffect } from 'react';
import { PStore } from '../../../store/PStore';
import PayPalButton from './PayPalButton';
import { PayPalScriptProvider } from "@paypal/react-paypal-js";
import './DeliveryPaypal.css';

const DeliveryPaypal = ({ currentUserId }) => {

    const [total, setTotal] = useState(null);

    const { basket_added } = useContext(PStore);

    useEffect(() => {
        let total = 0;
        basket_added.filter(bas => bas.IdUser === currentUserId).forEach(prod => {  
            total = total + Number(prod.ProductPrice);
        });
        setTotal((total).toFixed(2));
    }, [basket_added, currentUserId]);

    const initialOptions = {
        "client-id": "AXF0BIX46KclRfxn_hzofDgOTQMJQx_dUd40QCzQeiPFmprxkR7IeduTeIO-yXIBrsQEduwfxJKRngSz",
        intent: "capture",
        value: total,
        description: currentUserId
    };

    return(
        <div className="deliveryPaypal">
            <div className="deliveryPaypal-box">
                <PayPalScriptProvider options={initialOptions}>
                    <PayPalButton />
                </PayPalScriptProvider>    
            </div>
        </div>
    );
}

export default DeliveryPaypal;